const TableContext = createContext<{
  size: 'default' | 'small';
  someRowsSelected?: boolean;
}>({
  size: 'default',
});

export interface TableProps extends HTMLDataAttributes {
  size?: 'default' | 'small';
  /** Are any rows currently selected? You must specify this if using `TableRowSelectCell` in your table. */
  someRowsSelected?: boolean;
  /** Style property */
  style?: CSSProperties;
  /** Class name property */
  className?: string;
  /** Content slot for providing a pagination component */
  pagination?: React.ReactNode;
  /** Content slot for providing an Empty component */
  empty?: React.ReactNode;
  /** Child nodes for the table */
  children?: React.ReactNode | React.ReactNode[];
  /** Is this `Table` scrollable? Only use if `Table` is placed within a container of determinate height. */
  scrollable?: boolean;
}

export const Table = forwardRef<HTMLDivElement, TableProps>(function Table(
  { children, size = 'default', someRowsSelected, style, pagination, empty, className, scrollable = false, ...rest },
  ref
): JSX.Element {
  const { theme } = useDesignSystemTheme();
  const flags = useDesignSystemFlags();

  return (
    <TableContext.Provider value={{ size, someRowsSelected }}>
      <div
        {...rest}
        // This is a performance optimization; we want to statically create the styles for the table,
        // but for the dynamic theme values, we need to use CSS variables.
        // See: https://emotion.sh/docs/best-practices#advanced-css-variables-with-style
        style={{
          ...style,
          ['--table-background-color' as any]: theme.colors.backgroundPrimary,
          ['--table-scroll-shadow-color' as any]: theme.isDarkMode
            ? 'rgba(255, 255, 255, 0.07)'
            : 'rgba(31, 39, 45, 0.15)',
          ['--table-header-active-color' as any]: theme.colors.actionDefaultTextPress,
          ['colorScheme' as any]: theme.isDarkMode ? 'dark' : undefined,
          // This hex is pulled directly from the old table as a temporary style-matching measure.
          ['--table-header-background-color' as any]:
            flags.USE_UPDATED_TABLE_STYLES || theme.isDarkMode ? theme.colors.backgroundPrimary : '#F2F5F7',
          ['--table-header-focus-color' as any]: theme.colors.actionDefaultTextHover,
          ['--table-header-sort-icon-color' as any]: theme.colors.textSecondary,
          ['--table-header-text-color' as any]: theme.colors.actionDefaultTextDefault,
          ['--table-row-hover' as any]: theme.colors.tableRowHover,
          ['--table-separator-color' as any]: theme.colors.borderDecorative,
          ['--table-resize-handle-color' as any]: flags.USE_UPDATED_TABLE_STYLES
            ? theme.colors.borderDecorative
            : theme.colors.grey400,
          ['--table-spacing-md' as any]: `${theme.spacing.md}px`,
          ['--table-spacing-sm' as any]: `${theme.spacing.sm}px`,
          ['--table-spacing-xs' as any]: `${theme.spacing.xs}px`,
        }}
        css={tableStyles.tableWrapper}
        className={scrollable ? `table-isScrollable ${className}` : className}
      >
        <div
          role="table"
          ref={ref}
          css={tableStyles.table}
          style={
            // TODO: The static pixel values here are for the top of the table, but this won't work
            // for headers with variable height (in those cases the shadow will be hidden behind the larger header)
            // We need to find a way to make this dynamic.
            size === 'small'
              ? {
                  backgroundPosition: `
                    center 25px,
                    center bottom,
                    left center,
                    right center,
                    center 25px,
                    center bottom,
                    left center,
                    right center
                  `,
                }
              : {
                  backgroundPosition: `
                    center 36px,
                    center bottom,
                    left center,
                    right center,
                    center 36px,
                    center bottom,
                    left center,
                    right center
                  `,
                }
          }
        >
          {children}
          {empty && <div css={{ padding: theme.spacing.lg }}>{empty}</div>}
        </div>
        {!empty && pagination && <div css={tableStyles.paginationContainer}>{pagination}</div>}
      </div>
    </TableContext.Provider>
  );
});

const TableRowContext = createContext<{
  isHeader: boolean;
}>({
  isHeader: false,
});

export interface TableRowProps extends HTMLDataAttributes {
  /** Style property */
  style?: CSSProperties;
  /** Class name property */
  className?: string;
  /** Set to true if this row is to be used for a table header row */
  isHeader?: boolean;
  /** @deprecated Vertical alignment of the row's cells. No longer supported (See FEINF-1937) */
  // eslint-disable-next-line react/no-unused-prop-types
  verticalAlignment?: 'top' | 'center';
  /** Child nodes for the table row */
  children?: React.ReactNode | React.ReactNode[];
}

export const TableRow = forwardRef<HTMLDivElement, TableRowProps>(function TableRow(
  { children, className, style, isHeader = false, verticalAlignment, ...rest },
  ref
) {
  const { size } = useContext(TableContext);
  const { theme } = useDesignSystemTheme();

  // Vertical only be larger if the row is a header AND size is large.
  const shouldUseLargeVerticalPadding = isHeader && size === 'default';

  let rowPadding;

  if (shouldUseLargeVerticalPadding) {
    rowPadding = theme.spacing.sm;
  } else if (size === 'default') {
    rowPadding = 6;
  } else {
    rowPadding = theme.spacing.xs;
  }

  return (
    <TableRowContext.Provider value={{ isHeader }}>
      <div
        {...rest}
        ref={ref}
        css={[tableStyles.row]}
        role="row"
        style={{
          ...style,
          ['--table-row-vertical-padding' as any]: `${rowPadding}px`,
        }}
        className={`${className} ${isHeader ? 'table-isHeader' : ''}`}
      >
        {children}
      </div>
    </TableRowContext.Provider>
  );
});

export interface TableCellProps extends HTMLDataAttributes {
  /** Enables single-line ellipsis truncation */
  ellipsis?: boolean;
  /** How to horizontally align the cell contents */
  align?: 'left' | 'center' | 'right';
  /** Class name property */
  className?: string;
  /** Style property */
  style?: CSSProperties;
  /** Child nodes for the table cell */
  children?: React.ReactNode | React.ReactNode[];
}

export const TableCell = forwardRef<HTMLDivElement, TableCellProps>(function (
  { children, className, ellipsis = false, align = 'left', style, ...rest },
  ref
) {
  const { size } = useContext(TableContext);

  let typographySize: TextProps['size'] = 'md';

  if (size === 'small') {
    typographySize = 'sm';
  }

  return (
    <div
      {...rest}
      css={[tableStyles.cell]}
      role="cell"
      style={{ textAlign: align, ...style }}
      ref={ref}
      className={className}
    >
      <Typography.Text
        ellipsis={ellipsis}
        size={typographySize}
        title={(ellipsis && typeof children === 'string' && children) || undefined}
      >
        {children}
      </Typography.Text>
    </div>
  );
});

export interface TableHeaderProps extends HTMLDataAttributes {
  /** Enables single-line ellipsis truncation */
  ellipsis?: boolean;
  /** Is this column sortable? */
  sortable?: boolean;
  /** The current sort direction for this column */
  sortDirection?: 'asc' | 'desc' | 'none';
  /** Callback for when the user requests to toggle `sortDirection` */
  onToggleSort?: (event: unknown) => void;
  /** Style property */
  style?: CSSProperties;
  /** Class name property */
  className?: string;
  /** Child nodes for the table header */
  children?: React.ReactNode | React.ReactNode[];
  /** Whether the table header should include a resize handler */
  resizable?: boolean;
  /** Event handler to be passed down to <TableHeaderResizeHandle /> */
  resizeHandler?: React.PointerEventHandler<HTMLDivElement>;
  /** Whether the header is currently being resized */
  isResizing?: boolean;
  /** How to horizontally align the cell contents */
  align?: 'left' | 'center' | 'right';
}

export const TableHeader = forwardRef<HTMLDivElement, TableHeaderProps>(function TableHeader(
  {
    children,
    ellipsis = false,
    sortable,
    sortDirection,
    onToggleSort,
    style,
    className,
    resizable,
    resizeHandler,
    isResizing = false,
    align = 'left',
    ...rest
  },
  ref
) {
  const { size } = useContext(TableContext);
  const { isHeader } = useContext(TableRowContext);

  if (!isHeader) {
    throw new Error('`TableHeader` must be used within a `TableRow` with `isHeader` set to true.');
  }

  let sortIcon = <></>;
  // While most libaries use `asc` and `desc` for the sort value, the ARIA spec
  // uses `ascending` and `descending`.
  let ariaSort: React.AriaAttributes['aria-sort'];

  if (sortable) {
    if (sortDirection === 'asc') {
      sortIcon = <SortAscendingIcon />;
      ariaSort = 'ascending';
    } else if (sortDirection === 'desc') {
      sortIcon = <SortDescendingIcon />;
      ariaSort = 'descending';
    } else if (sortDirection === 'none') {
      sortIcon = <SortUnsortedIcon />;
      ariaSort = 'none';
    }
  }

  let typographySize: TextProps['size'] = 'md';

  if (size === 'small') {
    typographySize = 'sm';
  }

  const textContents = (
    <Typography.Text
      className="table-header-text"
      ellipsis={ellipsis}
      size={typographySize}
      title={(ellipsis && typeof children === 'string' && children) || undefined}
    >
      {children}
    </Typography.Text>
  );

  const resizeHandle = resizable ? <TableHeaderResizeHandle resizeHandler={resizeHandler} /> : null;

  return (
    <div
      {...rest}
      ref={ref}
      css={[tableStyles.cell, tableStyles.header]}
      role="columnheader"
      aria-sort={(sortable && ariaSort) || undefined}
      className={className}
      style={{
        justifyContent: align,
        textAlign: align,
        ...style,
      }}
    >
      {sortable && !isResizing ? (
        <div
          css={[tableStyles.headerButtonTarget]}
          role="button"
          tabIndex={0}
          onClick={onToggleSort}
          onKeyDown={(event) => {
            if (sortable && (event.key === 'Enter' || event.key === ' ')) {
              event.preventDefault();
              return onToggleSort?.(event);
            }
          }}
        >
          {textContents}
          <span className="table-header-icon-container" css={[tableStyles.sortHeaderIcon]}>
            {sortIcon}
          </span>
        </div>
      ) : (
        textContents
      )}
      {resizeHandle}
    </div>
  );
});

interface TableHeaderResizeHandleProps extends HTMLDataAttributes {
  /** Style property */
  style?: CSSProperties;
  /** Pass a handler to be called on pointerDown */
  resizeHandler?: React.PointerEventHandler<HTMLDivElement>;
}

const TableHeaderResizeHandle = forwardRef<HTMLDivElement, TableHeaderResizeHandleProps>(
  function TableHeaderResizeHandle({ style, resizeHandler, children, ...rest }, ref) {
    const { isHeader } = useContext(TableRowContext);

    if (!isHeader) {
      throw new Error('`TableHeaderResizeHandle` must be used within a `TableRow` with `isHeader` set to true.');
    }

    return (
      <div
        {...rest}
        ref={ref}
        onPointerDown={resizeHandler}
        css={tableStyles.resizeHandleContainer}
        style={style}
        role="separator"
      >
        <div css={tableStyles.resizeHandle} />
      </div>
    );
  }
);

interface TableRowSelectCellProps extends HTMLDataAttributes {
  /** Called when the checkbox is clicked */
  onChange?: (event: unknown) => void;
  /** Whether the checkbox is checked */
  checked?: boolean;
  /** Whether the row is indeterminate. Should only be used in header rows. */
  indeterminate?: boolean;
  /** Don't render a checkbox; used for providing spacing in header if you don't want "Select All" functionality */
  noCheckbox?: boolean;
}

export const TableRowSelectCell = forwardRef<HTMLDivElement, TableRowSelectCellProps>(function TableRowSelectCell(
  { onChange, checked, indeterminate, noCheckbox, children, ...rest },
  ref
) {
  const { isHeader } = useContext(TableRowContext);
  const { someRowsSelected } = useContext(TableContext);

  if (typeof someRowsSelected === 'undefined') {
    throw new Error(
      '`TableRowSelectCell` cannot be used unless `someRowsSelected` has been provided to the `Table` component, see documentation.'
    );
  }

  if (!isHeader && indeterminate) {
    throw new Error('`TableRowSelectCell` cannot be used with `indeterminate` in a non-header row.');
  }

  return (
    <div
      {...rest}
      ref={ref}
      css={tableStyles.checkboxCell}
      style={{
        ['--row-checkbox-opacity' as any]: someRowsSelected ? 1 : 0,
      }}
      role={isHeader ? 'columnheader' : 'cell'}
      // TODO: Ideally we shouldn't need to specify this `className`, but it allows for row-hovering to reveal
      // the checkbox in `TableRow`'s CSS without extra JS pointerin/out events.
      className="table-row-select-cell"
    >
      {!noCheckbox && (
        <Checkbox
          isChecked={checked || (indeterminate && null)}
          onChange={(_checked, event) => onChange?.(event.nativeEvent)}
        />
      )}
    </div>
  );
});

export interface TableSkeletonProps extends HTMLDataAttributes {
  /** Number of rows to render */
  lines?: number;
  /** Seed that deterministically arranges the uneven lines, so that they look like ragged text.
   * If you don't provide this (or give each skeleton the same seed) they will all look the same. */
  seed?: string;
  /** Style property */
  style?: CSSProperties;
}

const TableSkeletonStyles = {
  container: css({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
  }),

  cell: css({
    width: '100%',
    height: 8,
    borderRadius: 4,
    background: 'var(--table-skeleton-color)',
    marginTop: 'var(--table-skeleton-row-vertical-margin)',
    marginBottom: 'var(--table-skeleton-row-vertical-margin)',
  }),
};

export const TableSkeleton: React.FC<TableSkeletonProps> = ({
  lines = 1,
  seed = '',
  style,
  // TODO: Re-enable this after Clusters fixes tests: https://databricks.slack.com/archives/C04LYE3F8HX/p1679597678339659
  /** children, ...rest */
}) => {
  const { theme } = useDesignSystemTheme();
  const { size } = useContext(TableContext);

  // This is a very simple PRNG that is seeded (so that the output is deterministic).
  // We need this in order to produce a random ragged edge for the table skeleton.
  function pseudoRandomNumberGeneratorFromSeed(seed: string) {
    // This is a simple way to get a consistent number from a string;
    // `charCodeAt` returns a number between 0 and 65535, and we then just add them all up.
    const seedValue = seed
      .split('')
      .map((char) => char.charCodeAt(0))
      .reduce((prev, curr) => prev + curr, 0);

    // This is a simple sine wave function that will always return a number between 0 and 1.
    // This produces a value akin to `Math.random()`, but has deterministic output.
    // Of course, sine curves are not a perfectly random distribution between 0 and 1, but
    // it's close enough for our purposes.
    return Math.sin(seedValue) / 2 + 0.5;
  }

  // This is a simple Fisher-Yates shuffler using the above PRNG.
  function shuffleArray<T>(arr: T[], seed: string): T[] {
    for (let i = arr.length - 1; i > 0; i--) {
      const j = Math.floor(pseudoRandomNumberGeneratorFromSeed(seed + String(i)) * (i + 1));
      [arr[i], arr[j]] = [arr[j], arr[i]];
    }
    return arr;
  }

  // Finally, we shuffle a list off offsets to apply to the widths of the cells.
  // This ensures that the cells are not all the same width, but that they are
  // random to produce a more realistic looking skeleton.
  const widths = shuffleArray<number>([48, 24, 0], seed);

  return (
    <div
      // TODO: Re-enable this after Clusters fixes tests: https://databricks.slack.com/archives/C04LYE3F8HX/p1679597678339659
      // {...rest}
      css={TableSkeletonStyles.container}
      style={{
        ...style,
        // TODO: Pull this from the themes; it's not currently available.
        ['--table-skeleton-color' as any]: theme.isDarkMode ? 'rgba(255, 255, 255, 0.1)' : 'rgba(31, 38, 45, 0.1)',
        ['--table-skeleton-row-vertical-margin' as any]: size === 'small' ? '4px' : '6px',
      }}
    >
      {[...Array(lines)].map((_, idx) => (
        <div key={idx} css={[TableSkeletonStyles.cell, { width: `calc(100% - ${widths[idx % widths.length]}px)` }]} />
      ))}
    </div>
  );
};

export interface TableRowActionProps extends HTMLDataAttributes {
  /** Style property */
  style?: CSSProperties;
  /** Child nodes for the table row. Should contain a single small-sized button. */
  children?: React.ReactNode;
  /** Class name property */
  className?: string;
}

const TableRowActionStyles = {
  container: css({
    width: 32,
    paddingTop: 0,
    paddingBottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }),
};

export const TableRowAction = forwardRef<HTMLDivElement, TableRowActionProps>(function TableRowAction(
  { children, style, className, ...rest },
  ref
) {
  return (
    <div {...rest} ref={ref} css={TableRowActionStyles.container} style={style} className={className}>
      {children}
    </div>
  );
});

/** @deprecated Use `TableRowAction` instead */
export const TableRowMenuContainer = TableRowAction;

export interface TableFilterLayoutProps extends HTMLDataAttributes {
  /** Style property */
  style?: CSSProperties;
  /** Should contain the filter controls to be rendered in this layout. */
  children?: React.ReactNode;
  /** Class name property */
  className?: string;
  /** A container to hold action `Button` elements. */
  actions?: React.ReactNode;
}

const tableFilterLayoutStyles = {
  layout: css({
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 'var(--table-filter-layout-group-margin)',
  }),
  filters: css({
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    alignItems: 'center',
    gap: 'var(--table-filter-layout-item-gap)',
    flex: 1,
  }),
  filterActions: css({
    display: 'flex',
    gap: 'var(--table-filter-layout-item-gap)',
    marginLeft: 'var(--table-filter-layout-group-margin)',
  }),
};

export const TableFilterLayout = forwardRef<HTMLDivElement, TableFilterLayoutProps>(function TableFilterLayout(
  { children, style, className, actions, ...rest },
  ref
) {
  const { theme } = useDesignSystemTheme();

  return (
    <div
      {...rest}
      ref={ref}
      style={{
        ['--table-filter-layout-item-gap' as any]: `${theme.spacing.sm}px`,
        ['--table-filter-layout-group-margin' as any]: `${theme.spacing.md}px`,
        ...style,
      }}
      css={tableFilterLayoutStyles.layout}
      className={className}
    >
      <div css={tableFilterLayoutStyles.filters}>{children}</div>
      {actions && <div css={tableFilterLayoutStyles.filterActions}>{actions}</div>}
    </div>
  );
});

interface TableFilterInputProps extends InputProps, HTMLDataAttributes {
  // Called when the form is submitted, either by pressing enter or clicking the search button.
  onSubmit?: () => void;
  // Called when the input is cleared by clicking the (X) button.
  onClear?: () => void;
  // Whether to show the search button.
  showSearchButton?: boolean;
}

export const TableFilterInput = forwardRef<AntDInput, TableFilterInputProps>(function SearchInput(
  { onSubmit, showSearchButton, className, onChange, onClear, ...inputProps },
  ref
) {
  const DEFAULT_WIDTH = 350;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    // If the input is cleared, call the onClear handler, but only
    // if the event is not an input event -- which is the case when you click the
    // ant-provided (X) button.
    if (!e.target.value && e.nativeEvent instanceof InputEvent === false && onClear) {
      onClear?.();
    } else {
      onChange?.(e);
    }
  };

  let component = (
    <Input
      prefix={<SearchIcon />}
      allowClear
      {...inputProps}
      className={className}
      ref={ref}
      css={{
        width: DEFAULT_WIDTH,
      }}
      onChange={handleChange}
    />
  );

  if (showSearchButton) {
    component = (
      <Input.Group
        style={{
          display: 'flex',
        }}
        css={{
          width: DEFAULT_WIDTH,
        }}
        className={className}
      >
        <Input
          allowClear
          {...inputProps}
          ref={ref}
          css={{
            flex: 1,
          }}
          onChange={handleChange}
        />
        <Button htmlType="submit">
          <SearchIcon />
        </Button>
      </Input.Group>
    );
  }

  return (
    <form
      onSubmit={(e) => {
        e.preventDefault();
        onSubmit?.();
      }}
    >
      {component}
    </form>
  );
});
