import React, { forwardRef } from 'react';

import { useDesignSystemTheme } from '../Hooks';
import { SearchIcon } from '../Icon';
import type { InputProps, InputRef } from '../Input';
import { Input } from '../Input';
import { DialogComboboxOptionListCheckboxItem } from './DialogComboboxOptionListCheckboxItem';
import { DialogComboboxOptionListSelectItem } from './DialogComboboxOptionListSelectItem';
import { useDialogComboboxOptionListContext } from './hooks/useDialogComboboxOptionListContext';

const filterChildren = (children: any, searchValue: string) => {
  return React.Children.map(children, (child) => {
    if (child.type === DialogComboboxOptionListSelectItem || child.type === DialogComboboxOptionListCheckboxItem) {
      if (child?.props?.value?.toLowerCase().includes(searchValue)) {
        return child;
      }
      return null;
    }
    return child;
  })?.filter((child: any) => child);
};

export const DialogComboboxOptionListSearch = forwardRef<
  InputRef,
  InputProps & {
    children: any;
    hasWrapper?: boolean;
    virtualized?: boolean;
    onSearch?: (value: string) => void;
  }
>(({ onChange, onSearch, virtualized, children, hasWrapper, ...restProps }, forwardedRef) => {
  const { theme } = useDesignSystemTheme();
  const { isInsideDialogComboboxOptionList } = useDialogComboboxOptionListContext();

  const [searchValue, setSearchValue] = React.useState<string>();

  if (!isInsideDialogComboboxOptionList) {
    throw new Error('`DialogComboboxOptionListSearch` must be used within `DialogComboboxOptionList`');
  }

  const handleOnChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (!virtualized) {
      setSearchValue(event.target.value.toLowerCase());
    }

    onSearch?.(event.target.value);
  };

  let filteredChildren = children;

  if (searchValue && !virtualized) {
    filteredChildren = filterChildren(hasWrapper ? children.props.children : children, searchValue);

    if (hasWrapper) {
      filteredChildren = React.cloneElement(children, {}, filteredChildren);
    }
  }

  return (
    <>
      <div
        css={{
          padding: `${theme.spacing.sm}px ${theme.spacing.lg / 2}px ${theme.spacing.sm}px`,
          width: '100%',
          boxSizing: 'border-box',
        }}
      >
        <Input
          type="search"
          name="search"
          ref={forwardedRef}
          prefix={<SearchIcon />}
          placeholder="Search"
          onChange={handleOnChange}
          value={searchValue}
          {...restProps}
        />
      </div>
      {virtualized ? (
        children
      ) : (hasWrapper && filteredChildren?.props.children?.length) || (!hasWrapper && filteredChildren?.length) ? (
        <div aria-live="polite">{filteredChildren}</div>
      ) : (
        <div
          aria-live="assertive"
          css={{
            color: theme.colors.textSecondary,
            textAlign: 'center',
            padding: '6px 32px 6px 12px',
            width: '100%',
            boxSizing: 'border-box',
          }}
        >
          No results found
        </div>
      )}
    </>
  );
});
