import { forwardRef } from 'react';

import { useDesignSystemTheme } from '../Hooks';
import { CheckIcon } from '../Icon';
import type { HTMLDataAttributes } from '../types';
import { useDialogComboboxOptionListContext } from './hooks/useDialogComboboxOptionListContext';
import { getDialogComboboxOptionItemWrapperStyles } from './shared';

export interface DialogComboboxOptionListSelectItemProps
  extends HTMLDataAttributes,
    React.HTMLAttributes<HTMLDivElement> {
  value: string;
  checked?: boolean;
  children?: React.ReactNode;
  onChange?: (...args: any[]) => any;
}

export const DialogComboboxOptionListSelectItem = forwardRef<HTMLDivElement, DialogComboboxOptionListSelectItemProps>(
  ({ value, checked, onChange, children, ...props }, ref) => {
    const { theme } = useDesignSystemTheme();
    const { isInsideDialogComboboxOptionList } = useDialogComboboxOptionListContext();

    if (!isInsideDialogComboboxOptionList) {
      throw new Error('`DialogComboboxOptionListSelectItem` must be used within `DialogComboboxOptionList`');
    }

    const handleSelect = () => {
      if (onChange) {
        onChange(value);
      }
    };

    return (
      <div
        ref={ref}
        css={[
          getDialogComboboxOptionItemWrapperStyles(theme),
          {
            '&:focus': {
              background: theme.colors.actionTertiaryBackgroundHover,
              outline: 'none',
            },
          },
        ]}
        {...props}
        onClick={handleSelect}
        tabIndex={0}
        onKeyDown={(e) => {
          switch (e.key) {
            case 'ArrowDown':
              e.preventDefault();
              const nextSibling = e.currentTarget.nextElementSibling as HTMLElement;
              nextSibling?.focus();
              break;
            case 'ArrowUp':
              e.preventDefault();
              const previousSibling = e.currentTarget.previousElementSibling as HTMLElement;
              previousSibling?.focus();
              break;
            case ' ':
            case 'Enter':
              e.preventDefault();
              handleSelect();
              break;
          }
        }}
        role="option"
        aria-selected={checked}
        aria-label={value}
      >
        {checked ? <CheckIcon /> : <div css={{ width: 16 }} />}
        <label
          style={{
            fontSize: theme.typography.fontSizeBase,
            fontStyle: 'normal',
            fontWeight: 400,
            marginLeft: theme.spacing.sm,
            cursor: 'pointer',
          }}
        >
          {children ?? value}
        </label>
      </div>
    );
  }
);
