import type { Theme, SerializedStyles } from '@emotion/react';
import { css } from '@emotion/react';
import * as Popover from '@radix-ui/react-popover';
import { forwardRef } from 'react';

import { useDesignSystemTheme } from '../Hooks';
import { useDesignSystemContext } from '../Hooks/useDesignSystemContext';
import type { HTMLDataAttributes } from '../types';
import { DialogComboboxLoadingSpinner } from './DialogComboboxLoadingSpinner';
import { useDialogComboboxContext } from './hooks/useDialogComboboxContext';

export interface DialogComboboxContentProps extends Popover.PopoverContentProps, HTMLDataAttributes {
  width?: number;
  loading?: boolean;
  maxHeight?: number;
  maxWidth?: number;
  minHeight?: number;
  minWidth?: number;
  side?: 'top' | 'bottom';
}

const getContentWrapperStyles = (
  theme: Theme,
  {
    maxHeight = '100vh',
    maxWidth = '100vw',
    minHeight = 0,
    minWidth = 0,
  }: {
    maxHeight?: number | string;
    maxWidth?: number | string;
    minHeight?: number | string;
    minWidth?: number | string;
  }
): SerializedStyles => {
  return css({
    maxHeight,
    maxWidth,
    minHeight,
    minWidth,
    background: theme.colors.backgroundPrimary,
    color: theme.colors.textPrimary,
    overflow: 'auto',
    // Making sure the content popover overlaps the remove button when opens to the right
    zIndex: theme.options.zIndexBase + 10,

    boxSizing: 'border-box',
    padding: `${theme.spacing.xs}px 0`,

    border: `1px solid ${theme.colors.border}`,
    boxShadow: theme.general.shadowLow,
    borderRadius: 4,

    colorScheme: theme.isDarkMode ? 'dark' : 'light',
  });
};

export const DialogComboboxContent = forwardRef<HTMLDivElement, DialogComboboxContentProps>(
  (
    {
      children,
      loading,
      maxHeight,
      maxWidth,
      minHeight,
      minWidth = 240,
      align = 'start',
      side = 'bottom',
      sideOffset = 4,
      ...restProps
    },
    forwardedRef
  ) => {
    const { theme } = useDesignSystemTheme();
    const { label, isInsideDialogCombobox } = useDialogComboboxContext();
    const { getPopupContainer } = useDesignSystemContext();

    if (!isInsideDialogCombobox) {
      throw new Error('`DialogComboboxContent` must be used within `DialogCombobox`');
    }

    return (
      <Popover.Portal container={getPopupContainer && getPopupContainer()}>
        <Popover.Content
          aria-label={`${label} options`}
          aria-busy={loading}
          css={getContentWrapperStyles(theme, {
            maxHeight,
            maxWidth,
            minHeight,
            minWidth,
          })}
          align={align}
          side={side}
          sideOffset={sideOffset}
          {...restProps}
          ref={forwardedRef}
        >
          <div css={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'center' }}>
            {loading ? <DialogComboboxLoadingSpinner aria-label="Loading" alt="Loading spinner" /> : children}
          </div>
        </Popover.Content>
      </Popover.Portal>
    );
  }
);
