import { forwardRef } from 'react';

import type { HTMLDataAttributes } from '../types';
import { DialogComboboxLoadingSpinner } from './DialogComboboxLoadingSpinner';
import { useDialogComboboxContext } from './hooks/useDialogComboboxContext';
import { DialogComboboxOptionListContextProvider } from './providers/DialogComboboxOptionListContext';

export interface DialogComboboxOptionListProps extends HTMLDataAttributes, React.HTMLAttributes<HTMLDivElement> {
  children: any;
  loading?: boolean;
  withProgressiveLoading?: boolean;
}

export const DialogComboboxOptionList = forwardRef<HTMLDivElement, DialogComboboxOptionListProps>(
  ({ children, loading, withProgressiveLoading, ...restProps }, forwardedRef) => {
    const { isInsideDialogCombobox } = useDialogComboboxContext();

    if (!isInsideDialogCombobox) {
      throw new Error('`DialogComboboxOptionList` must be used within `DialogCombobox`');
    }

    return (
      <div
        ref={forwardedRef}
        aria-busy={loading}
        role="list"
        css={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'flex-start',
          width: '100%',
        }}
        {...restProps}
      >
        <DialogComboboxOptionListContextProvider value={{ isInsideDialogComboboxOptionList: true }}>
          {loading ? (
            withProgressiveLoading ? (
              <>
                {children}
                <DialogComboboxLoadingSpinner aria-label="Loading" alt="Loading spinner" />
              </>
            ) : (
              <DialogComboboxLoadingSpinner aria-label="Loading" alt="Loading spinner" />
            )
          ) : (
            children
          )}
        </DialogComboboxOptionListContextProvider>
      </div>
    );
  }
);
